.main_container_menu {
    transition: width 0.5s ease-in;
}

.main_menu {
    list-style: none;
    margin: 0;
    padding: 0;

}

#button_cache_menu {
    border: none;
    width: 40px;
    height: 40px;
}



.main_menu li {
    height: 10vh;
    min-height: 100px;
    margin: 10vh auto;
}

.child_menu {
    display: inline-flex;
    background-color: brown;
}

#kit_menu {
    display: none;
}

#file_menu {
    display: none;
}

.button_menu {
    height: 40px;
    width: 40px;
    margin: 3vh;
}

@media screen and (max-width: 1280px) {
    article {
        display: flex;
    }

    .main_container_menu {
        float: none;
        position: absolute;
        top: 0;
        height: 15vh;
        /* width: fit-content; */
        background-color: green;
    }

    .main_menu {
        display: none;
    }

    #button_cache_menu {
        display: block;
    }

}


@media screen and (orientation: portrait) {
    .main_container_menu {
        float: none;
        position: absolute;
        top: 0;
        height: 5vh;
        /* background-color: rgb(119, 128, 0); */
    }
}

@media screen and (min-width: 1280px) {

    .main_container_menu {
        float: left;
        width: 20%;
        height: 85vh;
        background-color: yellow;

    }

    #button_cache_menu {
        display: none;
    }
}